import EditorButton from '../EditorButton'
import { useDispatch, useSelector } from 'react-redux'
import { setSelectedObject } from '../../actions';

function Deselect(props) {
  const selectedItem = useSelector(state => state.selectedObject)
  const dispatch = useDispatch()

  return (
    <div className="Deselect">
      <EditorButton
        clickAction={
          () => {
            dispatch(setSelectedObject(false))
          }}
        icon={<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M11 24v-2h-4v2h4zm8-22h3v3h2v-5h-5v2zm-19 15h2v-4h-2v4zm0-6h2v-4h-2v4zm2-6v-3h3v-2h-5v5h2zm22 2h-2v4h2v-4zm0 6h-2v4h2v-4zm-2 6v3h-3v2h5v-5h-2zm-17 3h-3v-3h-2v5h5v-2zm12 2v-2h-4v2h4zm-6-22v-2h-4v2h4zm6 0v-2h-4v2h4zm0 11h-10v-2h10v2z" /></svg>}
        tooltip={`Deselect ${selectedItem.type}`}
      />
    </div>
  )
}

export default Deselect